@import url('https://fonts.googleapis.com/css?family=Archivo+Black|IBM+Plex+Mono:700&display=swap');
html {
    background-color: #243D97;
    font-family: 'IBM Plex Mono', monospace;
    color: #1A3661;
}
h1 {
    font-family: 'Archivo Black', sans-serif;
    font-size: 50px;
    margin: 0;
}

.nav-container {
    display: flex;
    justify-content: space-between;
}

.navbar {
    display: flex;
    position: relative;
}

.total-items--container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FEC000;
    border-radius: 50%;
    height:45px;
    width: 45px;
    transform: rotate(15deg);
    position: absolute;
    right: -38px;
    bottom: -12px;
}

.total-items {
    font-family: 'Archivo Black', sans-serif;
    font-size: 20px;
}

.reset-btn {
    color: #0091D4;
    font-family: 'Archivo Black', sans-serif;
    text-transform: uppercase;
    font-size: 20px;
    padding: 0;
    background: unset;
    border: unset;
    box-sizing: border-box;
    transition: border-bottom 250ms ease;
}

.reset-btn:hover {
    border-bottom:5px solid #0091D4;
    cursor: pointer;
}

.horizontal-rule {
    background-color: #1A3661;
    height: 8px;
    margin: 25px 0;
}

.cart-container {
    background-color: #F9F2EA;
    width: 730px;
    min-height: 400px;
    margin: 8% auto;
    padding:50px;
}

.counter {
    display: flex;
    align-items: center;
}

.counter:not(:first-child) {
    margin-top: 20px;
}

.buttons-container {
    margin-left: auto;
    display: flex;
    align-items: center;
    min-height: 50px;
}

.counter button {
    display: inline-block;
    padding: unset;
    margin: 0 10px;
    background: unset;
    min-width: 50px;
    min-height: 40px;
    text-align: center;
    border: unset;
    font-family: 'IBM Plex Mono', monospace;
}

button.btn-default {
    color: #0091D4;
    font-size: 30px;
}

button.btn-minus {
    margin-top: -1px;
}

button.btn-delete {
    color: #1A3661;
    font-size: 20px;
    font-family: 'Archivo Black', sans-serif;
    margin-bottom: -3px;
}

.album-cover-container {
    width: 70px;
    height: 70px;
    overflow: hidden;
    margin-right: 20px;
}

.album-cover {
    width: 70px;
}

.album-title-container {
    width: 310px;
}

.badge {
    display: inline-block;
    background-color: #1A3661;
    color: #F9F2EA;
    text-align: center;
    padding: 10px 0;
    width: 45px;
    height: 20px;
}

